import { Component, OnInit, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { Membro } from '../models/membro.model';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { UtilityService } from '../services/utility.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MembroService } from '../services/membro.service';
import { startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-membro',
  templateUrl: './membro.component.html',
  styleUrls: ['./membro.component.scss']
})
export class MembroComponent implements OnInit {

  form: FormGroup;
  submitted: boolean = false;
  success: boolean = false;
  options: string[] = [];
  filteredOptions: Observable<string[]>;
  idParticipante: string = "";

  constructor(
    private formBuilder: FormBuilder,
    private util: UtilityService,
    private membroService: MembroService,
    public dialogRef: MatDialogRef<MembroComponent>,
    @Inject(MAT_DIALOG_DATA) public data: string)
    { 
      this.idParticipante = data;
      this.form = this.createFormGroup();      
    }

  ngOnInit() {
  }
  onCancelClick() {
    this.dialogRef.close();    
  }

  createFormGroup(membro?: Membro) {
    var builder = this.formBuilder.group({
      idParticipante: new FormControl(this.idParticipante),
      id: new FormControl(),
      nome: new FormControl(),
      parentesco: new FormControl(),
      idade: new FormControl(),
      telefone: new FormControl(),
      situacao: new FormControl(),
      email: new FormControl(),
      value: new FormControl()
    });

    if (membro != null) {
      var data: any = membro;
      builder.patchValue(data);
    }

    return builder;
  }

  onSubmit() {
    this.submitted = true;

    if (this.form.invalid) {
      return;
    }

    this.saveMembro();
    this.success = true;
  }

  saveMembro() {
    this.util.showLoading();

    debugger;
    if (this.form.value.id != null && this.form.value.id != '') {

      this.membroService.getCollectionReference(x => x.where('id', '==', this.form.value.id))
        .valueChanges().subscribe(data => {
          if (data.length == 0) {
            var newMembro = new Membro();
            newMembro.id = this.form.value.id;
            this.membroService.save(newMembro)
              .catch(() => {
                this.util.hideLoading();
              });
          } else {
            this.saveMembroData();
          }
        });
    } else {
      this.saveMembroData();
    }
  }

  private saveMembroData() {
   
    var newMembro = new Membro(
        this.form.value.id,
        this.idParticipante,
        this.form.value.nome,
        this.form.value.parentesco,
        this.form.value.idade,
        this.form.value.telefone,
        this.form.value.situacao,
        this.form.value.email,
      );

    this.membroService
      .save(newMembro)
      .catch(() => {
        this.util.hideLoading();
      })
      .then(() => {
        this.onCancelClick();
        this.util.hideLoading();   
      })
      .finally(() => {
        this.util.hideLoading();
      });;
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().includes(filterValue));
  }
}
