import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { Participantes } from '../models/participantes.model';
import { UtilityService } from '../services/utility.service';
import { ParticipanteComponent } from '../participante/participante.component';
import { ParticipanteService } from '../services/participante.service';


@Component({
  selector: 'app-participantes',
  templateUrl: './participantes.component.html',
  styleUrls: ['./participantes.component.scss']
})
export class ParticipantesComponent implements OnInit {
  displayedColumns: string[] = ['nomeChefe', 'situacaoNaIgreja', 'tipoVeiculo', 'edit', 'remove'];
  dataSource: MatTableDataSource<Participantes>;
  @ViewChild(MatSort) sort: MatSort;

  participantes: Participantes[];

  constructor(
              private dialog: MatDialog,
              private util: UtilityService,
              private participanteService: ParticipanteService)
  { }

  ngOnInit() {
    this.getParticipantes();
  }

  getParticipantes() {
    this.util.showLoading();

    const participantes = this.participanteService.getAllFromUser();

    participantes.subscribe(data => { this.participantes = data.sort(); this.fillDataSource(); });
  }

  fillDataSource() {
    if (this.participantes == null) {
      return;
    }

    this.dataSource = new MatTableDataSource(this.participantes);
    this.dataSource.sort = this.sort;

    this.dataSource.filterPredicate = (data, filter: string) => {
      const accumulator = (currentTerm, key) => {
        return this.nestedFilterCheck(currentTerm, data, key);
      };
      const dataStr = Object.keys(data).reduce(accumulator, '').toLowerCase();
      // Transform the filter by converting it to lowercase and removing whitespace.
      const transformedFilter = filter.trim().toLowerCase();
      return dataStr.indexOf(transformedFilter) !== -1;
    };

    this.util.hideLoading();
  }

  nestedFilterCheck(search, data, key) {
    if (typeof data[key] === 'object') {
      for (const k in data[key]) {
        if (data[key][k] !== null) {
          search = this.nestedFilterCheck(search, data[key], k);
        }
      }
    } else {
      search += data[key];
    }
    return search;
  }

  editExpense(id: string) {
    const dialogRef = this.dialog.open(ParticipanteComponent, {
      width: '550px',
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  removeExpense(id: string) {
    this.util.showLoading();
    this.participanteService
      .remove(id)
      .catch(() => {
      })
      .finally(() => {
        this.util.hideLoading();
      });
  }

}
