import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { startWith, map } from 'rxjs/operators';
import { Location } from '@angular/common';
import { Participantes } from '../models/participantes.model';
import { ParticipanteService } from '../services/participante.service';
import { UtilityService } from '../services/utility.service';
import { MembroComponent } from '../membro/membro.component';
import { Membro } from '../models/membro.model';
import { MembroService } from '../services/membro.service';

@Component({
  selector: 'app-participante',
  templateUrl: './participante.component.html',
  styleUrls: ['./participante.component.scss']
})
export class ParticipanteComponent implements OnInit {

  displayedColumns: string[] = ['nome', 'parentesco', 'telefone', 'situacao', 'remove'];
  form: FormGroup;
  submitted: boolean = false;
  success: boolean = false;
  options: string[] = [];
  filteredOptions: Observable<string[]>;
  membros: Membro[];
  dataSource: MatTableDataSource<Membro>;
  
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private formBuilder: FormBuilder,
    private util: UtilityService,
    private participanteService: ParticipanteService,
    private membroService: MembroService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private location: Location) {
      this.form = this.createFormGroup();
    }

  ngOnInit() {
    const id = this.route.snapshot.paramMap.get('id');

    this.participanteService.get(id.toString()).subscribe(data => {
      this.form = this.createFormGroup(data);
      });
      
      this.membroService.getMembrosPorParticipanteReference(id).valueChanges()
        .subscribe(data => {
          this.membros = data.sort(); this.fillDataSource();
        });

        this.membroService.getMembrosPorParticipanteReference(id).valueChanges()
        .subscribe(data => {
          data.forEach(value => {
            this.membros.push(value);
          })
        });
  }

  createFormGroup(participante?: Participantes) {
    var builder = this.formBuilder.group({
      id: new FormControl(),
      nomeChefe: new FormControl(),
      endereco: new FormControl(),
      complemento: new FormControl(),
      numero: new FormControl(),
      bairro: new FormControl(),
      telefone: new FormControl(),
      telefoneEmergencia: new FormControl(),
      email: new FormControl(),
      estadoCivil: new FormControl(),
      quantidadeMembroFamilia: new FormControl(),
      idade: new FormControl(),
      situacaoNaIgreja: new FormControl(),
      tipoVeiculo: new FormControl(),
      value: new FormControl()
    });

    if (participante != null) {
      var data: any = participante;
      builder.patchValue(data);
    }

    
    return builder;
  }

  newMembro() {
    const dialogRef = this.dialog.open(MembroComponent, {
      width: '650px', data : this.form.value.id
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
    });
  }

  removeExpense(id: string) {
    this.util.showLoading();
    this.membroService
      .remove(id)
      .catch(() => {
      })
      .finally(() => {
        this.util.hideLoading();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (this.form.invalid) {
      return;
    }

    this.saveParticipante();
    this.success = true;
  }

  saveParticipante() {
    this.util.showLoading();

    if (this.form.value.id != null && this.form.value.id != '') {

      this.participanteService.getCollectionReference(x => x.where('id', '==', this.form.value.id))
        .valueChanges().subscribe(data => {
          if (data.length == 0) {
            var newParticipante = new Participantes();
            newParticipante.id = this.form.value.id;

            this.participanteService.save(newParticipante)
              .catch(() => {
                this.util.hideLoading();
              });
          } else {
            this.saveParticipaneteData();
          }
        });
    } else {
      this.saveParticipaneteData();
    }
  }

  private saveParticipaneteData() {
    //var idCategory = this.form.value.idCategory;
    
    var newParticipante = new Participantes(
        this.form.value.id,
        this.form.value.nomeChefe,
        this.form.value.endereco,
        this.form.value.complemento,
        this.form.value.numero,
        this.form.value.bairro,
        this.form.value.telefone,
        this.form.value.telefoneEmergencia,
        this.form.value.email,
        this.form.value.estadoCivil,
        this.form.value.quantidadeMembroFamilia,
        this.form.value.idade,
        this.form.value.situacaoNaIgreja,
        this.form.value.tipoVeiculo
      );

    this.participanteService
      .save(newParticipante)
      .catch(() => {
        this.util.hideLoading();
      })
      .then(() => {
        this.form.value.id = newParticipante.id;
        this.util.hideLoading();
      })
      .finally(() => {
        this.util.hideLoading();
      });;
  }

  OnClickNovo() {
    this.form = this.createFormGroup();
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().includes(filterValue));
  }

  fillDataSource() {
    if (this.membros == null) {
      return;
    }

    this.dataSource = new MatTableDataSource(this.membros);
    this.dataSource.sort = this.sort;

    this.dataSource.filterPredicate = (data, filter: string) => {
      const accumulator = (currentTerm, key) => {
        return this.nestedFilterCheck(currentTerm, data, key);
      };
      const dataStr = Object.keys(data).reduce(accumulator, '').toLowerCase();
      // Transform the filter by converting it to lowercase and removing whitespace.
      const transformedFilter = filter.trim().toLowerCase();
      return dataStr.indexOf(transformedFilter) !== -1;
    };

    this.util.hideLoading();
  }
  nestedFilterCheck(search, data, key) {
    if (typeof data[key] === 'object') {
      for (const k in data[key]) {
        if (data[key][k] !== null) {
          search = this.nestedFilterCheck(search, data[key], k);
        }
      }
    } else {
      search += data[key];
    }
    return search;
  }
}
