import { UtilityService } from './utility.service';
import { Injectable } from '@angular/core';
import { BaseAngularService } from './base-angular.service';
import { Expense } from '../models/expense.model';
import { QueryFn, AngularFirestore } from '@angular/fire/firestore';
import { Participantes } from '../models/participantes.model';

@Injectable({
  providedIn: 'root'
})
export class ParticipanteService extends BaseAngularService<Participantes> {
  public relativeUrl: string;

  constructor(
              protected firestore: AngularFirestore,
              protected util: UtilityService) {
    super(firestore, util);
  }

  getCollectionReference(queryFn?: QueryFn) {
    return this.firestore.collection<Participantes>('participantes', queryFn);
  }
}
